import React from 'react';
import { Button, Table } from 'react-bootstrap';
import { connect } from 'react-redux';


function Cart (props) {

  return (
    <div className="container py-5 px-3">
      <Table striped bordered hover size="sm">
        <thead>
          <tr>
            <th>#</th>
            <th>상품명</th>
            <th>수량</th>
            <th>변경</th>
          </tr>
        </thead>
        <tbody>
          {
            props.state.map((cart,i) => {
              return (
                <tr key={cart.id + `${i}`}>
                  <td>{i + 1}</td>
                  <td>{cart.name}</td>
                  <td>{cart.quan}</td>
                  <td>
                    <button className="btn btn-primary" onClick={ () => { props.dispatch({type: 'INCREASE', numData: cart.id}) } }>+</button>
                    <button className="btn btn-outline-primary" onClick={ () => { props.dispatch({type: 'REDUCTION', numData: cart.id}) } }>-</button>
                    </td>
                </tr>
              )
            })
          }
        </tbody>
      </Table>

      {
        props.alertState
        ? <div className="my-alert2 text-center">
            <p> 지금 구매하시면 신규할인 20% </p>
            <Button onClick={() => { props.dispatch({type: 'CLOSEALERT'})}}> 닫기 </Button>
          </div>
        : null
      }
      
      
    </div>
  )
}

function statData(state) {
  return {
    state : state.reducer,
    alertState : state.reducer2
  }
}

export default connect(statData)(Cart);